@import './variables';

.wrapper, html, body {
  height: 100vh;
  margin: 0;
  padding:0;
  font-family: 'Montserrat' ,sans-serif;

   * { 
    &::-webkit-scrollbar {
      display: none;
  }
   }
}

.wrapper {
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.content {
  max-height: 100%;
  flex-grow: 5;
  flex-direction: row;
  flex-wrap: wrap;
  order: 1;
  white-space: nowrap;
}

 .header{
  display: flex;
  flex-basis: 100%;
  background: #c7c6c6;
  height: 80px;
  z-index: 2;
  top: 0;
  position: sticky;
    width: 100%;
    align-self: flex-start;
}

label{
  width: fit-content;
  /* flex-basis: 90%; */
  font-size: 30px;
  font-weight: 700;
  line-height: 66px;
  align-self: center;
  padding: 20px;
  color: #fcfcfc;
  background: #da5555;
  box-shadow: 0 1.4rem 8em rgba(0,0,0,.5);
}

.hamburger{
  align-content: flex-end;
  flex-basis: 70%;
  flex-grow: 5;
  display:none;
}

.hamburger:hover{
  background: #da5555;
}

.searchArea{
  display: flex;
  max-height: calc(100% - 90px);
    flex-direction: column;
  background-color: #fff;
  position: relative;
  overflow: hidden;
  height: inherit;

  .searchContent{
    max-height: calc(100% - 200px);
    overflow-y: auto;
    flex: 1;
    height: 500px;
  }
}



.search__container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 4rem 2rem 2rem 2rem;
  padding: 0 1.5rem;
}

.search__wrapper{
  background: white;
  width: 40vw;
  padding: 1em;
  position: sticky;
    align-self: flex-start;
  box-shadow: 0 0 3em rgba(0, 0, 0, 0.15);
}

.search-bar__input{
  width: 38vw;
  height: 1.5em;
  background: none;
  border: none;
  font-size: large;
  outline: none;
  border-bottom: .5px solid #413e3e;
  transition: all 1s ease-in-out;
  cursor: pointer;
}

.search-bar__input:focus{
  border-bottom: 2px solid #da5555;
}

.search__wrapper button{
  background: none;
  border: none;
  margin-left: 3px;
}

.search-bar__submit{
  background: none;
  color: #da5555
}

.booksWrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 1.5rem;
  margin: 0 1rem;
  overflow: hidden;
}

.books{
  width: 100%;
  max-width: 25rem;
  height: .7rem;
  padding: 5rem;
  margin: .5rem;
  background-color: #fff;
  box-shadow: 0 1.4rem 8em rgba(0,0,0,.2);
  display: flex;
  align-items: center;
  border-radius: .8rem;
}

.books__img{
  min-width: 6rem;
  max-width: 6rem;
  height: 9rem;
  transform: translateX(-6rem);
  position: relative;
  box-shadow: 0 1.4rem 8em rgba(0, 0, 0, 0.3);
}

.books__img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: .2rem;
}

.books__img::before{
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top:0;
  left:0;
  box-shadow: .5rem .5rem 3rem 1px rgba(0,0,0,.05);
  border-radius: .8rem;
}

.books__info{
  margin: .5rem -50px;
}

.books__info span{
  color: rgba(0,0,0,.5);
  font-size: 0.7rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
}

.books__title{
  font-size: 1rem;
  margin: 1rem 0 .5rem;
  text-transform: uppercase;
  color: #393a3a;
  max-width: 300px !important;
  text-shadow: 2px 3px 5px rgba(0,0,0,0.15);
  overflow: hidden;
  text-overflow: ellipsis;
}

.books__description{
  font-size: .7rem;
  color: rgba(0,0,0,.7);
  max-width: 400px !important;
  height: 2rem !important;
  overflow: hidden;
  line-height: 1em;
  text-overflow: ellipsis;
}

.book__searchbtn{
  display: inline-block;
  padding: .5rem .5rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: .8rem;
  color: #fff;
  background: #da5555;
  border: none;
  border-radius: .5rem;
}

.book__searchbtn:hover{
  background: #a32525;

}

@media screen and (max-width: 868px){
  .books{
    max-width: 10rem;
    white-space: normal;
  }

  .books__img{
    min-width: 5rem;
    max-width: 5rem;
  }

  .books__info span{
    display: none;
  }

  .books__description{
    display: none;
  }

  .books__title{
    font-size: small;;
    height: 2rem !important;
  }


}

.wishList{
  max-height: 100%;
  overflow: auto;
  display: flex;
  position: relative;
  flex-direction: column;
  flex-basis: 30%;
  order: 2;
  background:#0e0808;
}

.wishList:hover{
  box-shadow: 0 1.4rem 8em rgba(0,0,0,.7);
}

.nav-wishlist {
  z-index: 3;
  background: inherit;
  align-self: center;
  padding: 6px 15px;
  position: sticky;
  top:0;
  width: 100%;
  text-align: center;
}

.nav-wishlist .button{
  display: inline-block;
  height: 50px;
  margin: 0 5px;
  overflow:hidden;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0px,10px 10px rgba(0,0,0,0.1);
}



.wishlist :hover{
  width:200px;
}


.nav-wishlist .button .wishListIcon{
  display: inline-block;
  padding: 10px;
  height: 50px;
  width: 50px;
  border-radius: 10px;
  box-sizing: border-box;
  line-height: 40px;
  background-color:#da5555;
  color: white;
  margin-right: 20px;
}

.nav-wishlist .button .wishListIcon i{
  font-size: 5px;
  line-height: 40px;
}

.nav-wishlist .button span{
  font-size: 25px;
  font-weight: 500;
  line-height: 60px;
  margin:10px 10px 10px 10px;
  color: #da5555;
  text-shadow: 2px 3px 5px rgba(247, 244, 244, 0.15);
  vertical-align: text-bottom
}

.wish-book-list{
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  width: 100%;
  color:white;
}

.wishlist__searchbtn{
  padding-left: 2rem;
  background-color: #0e0808;
  color: #da5555;
  width: 30px;
  height: 30px;
  box-shadow: none;
  border: none;
  outline: none;
  text-shadow:2px 3px 5px rgba(247, 244, 244, 0.15) ;
}

.wishlist__searchbtn:hover{
  width: 35px;
  height:35px;
  color: #a83030;
}


.wishlist_each_item{
  align-items: center;
  display: flex;
  flex-direction: row;
  border: .5px solid #da5555;
  margin: .5rem;
  border-radius: 10px;
  justify-content: space-between;
  padding: 10px 16px;
}

.wishlist_each_item:hover{
  border: 1px solid #da5555;
  box-shadow: 0 1.4rem 8em rgba(255, 255, 255, 0.2);
}

.wishlist_title{
  max-width: 290px;
  line-height: 1.5rem;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-grow: 4;
}

